import Vue from 'vue';
import Vuex from 'vuex';

import mutations from './mutations.js';
import actions from './actions.js'
// 
Vue.use(Vuex);


const store = new Vuex.Store({
    // 
    state: {
        // 
        topics: [
            { name: "all", desc: "", select: true },
            { name: "good", desc: "" },
            { name: "share", desc: "" },
            { name: "ask", desc: "" },
            { name: "job", desc: "" },
            { name: "dev", desc: "" }
        ],
        //  
        unLoginedMoreMenus: {
            login: ""
        },
        //  
        loginedMoreMenus: {
            create: '',
            center: "",
            msg: "",
            logout: "",
        },
        // 
        isLogined: false,
        // 
        loginname: '',
        // 
        isShowMoreMenus: false,
        accesstoken: "",
        // id
        author_id: "",
    },
    // 
    getters: {
        moreMenus(state) {
            if (state.isLogined) {
                // 
                return state.loginedMoreMenus;
            }
            return state.unLoginedMoreMenus;
        },
        getTopicDescByTab(state) {
            // 
            return function (tab) {
                for (var index = 0; index < state.topics.length; index++) {
                    if (state.topics[index].name == tab) {
                        return state.topics[index].desc;
                    }
                }
            }
        }
    },
    mutations,
    actions
});

// 
export default store;


